html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  color: #ffffff;
  background-color: #1f1f1f;
  font-family: "Roboto", sans-serif;
}

.App {
  height: 100vh;
  overflow-y: auto;
}

input:focus, textarea:focus {
  outline: none;
}

textarea {
  resize: none;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0;
}

a,
a:visited,
a:active {
  font-size: 1rem;
  font-weight: 300;
}

h1 {
  font-size: 3rem;
  font-weight: 400;
}

h2 {
  font-size: 2.1875rem;
  font-weight: 300;
}

h3 {
  font-size: 1.25rem;
  font-weight: 400;
}

p,
span,
button,
input,
label,
textarea {
  font-size: 1rem;
  font-weight: 200;
}

main.navbar-included {
  position: absolute;
  margin-top: 3.75rem;
}

a {
  cursor: pointer;
}

button {
  height: 2.5rem;
  width: auto;
  padding: 0 1.875rem;
  background-color: #80e897;
  margin-right: 1.375rem;
  border: none;
  border-radius: 3.125rem;
}

button:disabled {
  background-color: #488956;
}

input,
label,
textarea {
  font-family: "Roboto", sans-serif;
  background-color: transparent;
  color: #ffffff;
  border: 2px solid #333333;
  height: 2.5rem;
  width: 30rem;
  padding: 0.688rem 0.938rem;
  border-radius: 0.75rem;
}

input::placeholder {
  color: rgba(255, 255, 255, 0.314);
}

.navbar {
  display: flex;
  height: 3.75rem;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 1.875rem;
  color: #1f1f1f;
  background-color: #1f1f1f;
  position: fixed;
  z-index: 2;
}
.navbar h1 {
  font-size: 1.875rem;
  color: #ffd675;
}
.navbar input {
  height: 2.5rem;
  width: 30rem;
  background-color: #ffffff;
  color: #1f1f1f;
  padding: 0.688rem 0.938rem;
  border: none;
  border-radius: 0.75rem;
}
.navbar input::placeholder {
  color: #1f1f1f;
}
.navbar input.user-search {
  width: 15.625rem;
  position: absolute;
  margin-left: 3.75rem;
}
.navbar .navlinks {
  display: flex;
  gap: 3.75rem;
}
.navbar .navlinks img {
  background-color: transparent;
}
.navbar .login-btn-parent {
  display: flex;
  align-items: center;
}
.navbar .login-btn-parent img {
  border-radius: 50%;
}
.navbar .login-btn-parent span {
  color: #80e897;
}
.navbar .login-btn-parent button {
  margin-left: 1.375rem;
}

main.landing-page {
  height: 100%;
  position: absolute;
}

section.hero-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  padding: 0 1.875rem;
}
section.hero-section img.hero-image {
  height: 90%;
  width: 45%;
  margin-right: 3.75rem;
}
section.hero-section .hero-text {
  width: 50%;
  color: #ffffff;
}
section.hero-section .hero-text h1 {
  font-size: 3rem;
}
section.hero-section .hero-text span.emphasized {
  font-size: 3rem;
  font-weight: 400;
  color: #ffd675;
}
section.hero-section .hero-text p {
  margin: 3.75rem 0;
}
section.hero-section .hero-text .login-btn-parent span {
  color: #80e897;
}
section.hero-section .hero-text .login-btn-parent button {
  margin-right: 1.375rem;
}

section.become-contributor img.hero-image {
  margin-left: 3.75rem;
  height: 70%;
}

section.keep-reading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3.75rem;
  height: 100vh;
}
section.keep-reading h1 {
  font-size: 3.75rem;
}
section.keep-reading h1.emphasized {
  color: #ffd675;
}
section.keep-reading p {
  text-align: center;
  width: 65%;
}

main.not-found {
  display: flex;
  position: static;
  height: calc(100vh - 3.75rem);
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3.75rem;
}
main.not-found h1.emphasized {
  color: #ffd675;
}
main.not-found span {
  color: #80e897;
}

.signup,
.login,
.reset-pass {
  height: 100%;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.signup h1,
.login h1,
.reset-pass h1 {
  color: #ffd675;
  margin-left: auto;
  margin-right: auto;
}
.signup form,
.login form,
.reset-pass form {
  margin-top: 3.75rem;
}
.signup input,
.login input,
.reset-pass input {
  display: block;
  margin-bottom: 0.9375rem;
}
.signup button,
.login button,
.reset-pass button {
  display: block;
  margin-top: 3.75rem;
  margin-left: auto;
  margin-right: auto;
}

main.upload {
  display: flex;
}
main.upload form.upload-form {
  width: 50%;
  height: 100vh;
  padding-left: 1.875rem;
  display: flex;
  justify-content: center;
  align-items: baseline;
  flex-direction: column;
}
main.upload form.upload-form input.gbooks-link,
main.upload form.upload-form label {
  margin-top: 0.9375rem;
  color: #ffffff;
  display: flex;
  align-items: center;
}
main.upload form.upload-form label {
  color: rgba(255, 255, 255, 0.314);
}
main.upload form.upload-form input[type=file] {
  display: none;
}
main.upload form.upload-form .buttons-container {
  display: flex;
  margin-top: 3.75rem;
}
main.upload form.upload-form .buttons-container button {
  display: block;
  margin-top: 0;
}
main.upload img.bookwall {
  width: 50%;
}

main.pdf-renderer {
  height: 100vh;
  display: grid;
  grid-template-columns: 30% auto 30%;
  align-items: center;
  padding: 0 1.875rem;
}
main.pdf-renderer .pdf-container {
  display: flex;
  flex-direction: column;
  grid-column-start: 2;
  height: 80%;
  align-items: center;
  justify-content: center;
  overflow: scroll;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}
main.pdf-renderer .pdf-container::-webkit-scrollbar {
  display: none;
}
main.pdf-renderer .pdf-container .annotationLayer {
  display: none;
}
main.pdf-renderer section.bookmarks {
  margin-left: 3.75rem;
}
main.pdf-renderer section.bookmarks .pdf-doc {
  width: 100%;
  overflow-x: scroll;
  display: flex;
  gap: 0.9375rem;
}
main.pdf-renderer section.bookmarks .pdf-doc canvas {
  border-radius: 0.5rem;
}
main.pdf-renderer p.page-number {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 1.875rem;
}
main.pdf-renderer img.close {
  background-color: #333333;
  border-radius: 50%;
  padding: 5px;
  position: fixed;
  top: 1.875rem;
  left: calc(3.75rem - 20px);
}
main.pdf-renderer aside {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.9375rem;
  position: fixed;
  top: 50%;
  left: 1.875rem;
  transform: translateY(-50%);
  width: 3.75rem;
  background-color: #333333;
  border-radius: 0.75rem;
  padding: 0.9375rem 3px;
}
main.pdf-renderer svg {
  width: 1.875rem;
  height: 1.875rem;
}
main.pdf-renderer section.page-comments {
  grid-column-start: 3;
  margin-left: 3.75rem;
}

main.book-details {
  position: absolute;
  padding: 0 1.875rem;
  width: 100%;
}
main.book-details section.main-details {
  display: flex;
  height: calc(100vh - 3.75rem);
  justify-content: center;
  align-items: center;
  gap: 7.5rem;
  margin-bottom: 7.5rem;
}
main.book-details section.main-details img {
  height: 30.625rem;
  border-radius: 0.75rem;
}
main.book-details section.main-details .text-details {
  display: flex;
  flex-direction: column;
  gap: 1.875rem;
}
main.book-details section.main-details .text-details .genres-container {
  display: flex;
  gap: 0.9375rem;
}
main.book-details section.main-details .text-details .genre {
  display: inline-block;
  height: 1.5rem;
  width: auto;
  padding: 0 0.9375rem;
  background-color: rgba(255, 213, 115, 0.408);
  border-radius: 3.125rem;
  border: 1px solid #ffd675;
}
main.book-details section.main-details .text-details .buttons-container span {
  color: #80e897;
}
main.book-details section.overview,
main.book-details section.more-books {
  display: inline-block;
  width: 50%;
}
main.book-details section.overview p {
  width: 90%;
}

.book-basic-details {
  width: fit-content;
  margin-right: 1.875rem;
}
.book-basic-details img {
  height: 14rem;
  width: 9rem;
  border-radius: 0.75rem;
  object-position: 0px 0px;
  object-fit: cover;
}
.book-basic-details span {
  display: block;
  margin-top: calc(0.9375rem - 5px);
  width: 90%;
  font-weight: 400;
}

.book-basic-details:hover {
  color: #ffd675;
  cursor: pointer;
}

main.explore {
  padding-left: 1.875rem;
}
main.explore section {
  margin-top: 3.75rem;
  margin-bottom: 0.9375rem;
}
main.explore .books-details-container {
  display: flex;
  margin-top: 1.875rem;
}
main.explore .authors-details-container {
  display: flex;
  margin-top: 1.875rem;
}

.author-basic-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 1.875rem;
}
.author-basic-details img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-position: 0px 0px;
  object-fit: cover;
}
.author-basic-details span {
  margin-top: calc(0.9375rem - 5px);
  width: 60%;
  font-weight: 400;
}
.author-basic-details span:hover {
  color: #ffd675;
  cursor: pointer;
}

main.search-page {
  margin-top: 3.75rem;
  padding-left: 1.875rem;
}
main.search-page .book-results {
  margin-top: 1.875rem;
}
main.search-page .book-results h2 {
  margin-bottom: 0.9375rem;
}
main.search-page .book-results-container {
  display: flex;
}

main.thread-page {
  width: 100%;
}

section.chatbox {
  height: calc(100vh - 3.75rem);
  padding: 0 1.875rem;
}
section.chatbox .messages-container {
  width: 100%;
  height: 80%;
  padding: 0 1.875rem;
  overflow-y: scroll;
}
section.chatbox .messages-container span {
  width: 100%;
}
section.chatbox form {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1f1f1f;
  width: auto;
}
section.chatbox input {
  width: 100%;
  margin: 0 auto;
}

.message {
  display: flex;
  gap: 0.9375rem;
  margin: 1.875rem 0;
}
.message img {
  border-radius: 3.125rem;
}
.message h3 {
  display: inline;
  font-size: 1rem;
}
.message span {
  font-size: 0.85rem;
  margin-left: calc(0.9375rem - 8px);
  color: #333333;
}
.message p {
  margin: calc(0.9375rem - 3px) 0;
}

section.reviews {
  margin-top: 7.5rem;
  margin-bottom: 3.75rem;
}
section.reviews h2 {
  display: flex;
  align-items: center;
}
section.reviews h2 div {
  display: inline-block;
  height: 2.8125rem;
  width: 3px;
  background-color: #ffd675;
  margin: 0 0.9375rem;
}
section.reviews textarea {
  width: 100%;
  line-height: 1;
  overflow-y: hidden;
}
section.reviews .review {
  display: flex;
  flex-direction: column;
  margin: 1.875rem 0;
}
section.reviews .review .img-and-name-container {
  display: flex;
  align-items: center;
}
section.reviews .review img {
  border-radius: 50%;
}
section.reviews .review p {
  width: 80%;
}

main.my-library {
  position: absolute;
  width: 100%;
  padding: 0 1.875rem;
}
main.my-library section.shelf {
  margin-top: 3.75rem;
}
main.my-library section.shelf .headline-and-icons-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 1.875rem;
}
main.my-library img.add-icon {
  padding: 5px;
}
main.my-library img.add-icon:hover {
  padding: 5px;
  border-radius: 0.5rem;
  background-color: #333333;
}
main.my-library .books {
  display: flex;
  gap: 1.875rem;
}